@use 'libs/vars';
@use 'libs/functions';
@use 'libs/mixins';
@charset "UTF-8";

 @use "libs/skel";

 @include skel.skel-breakpoints((
 wide: '(min-width: 961px) and (max-width: 1880px)',
 normal: '(min-width: 961px) and (max-width: 1620px)',
 narrow: '(min-width: 961px) and (max-width: 1320px)',
 narrower: '(max-width: 960px)',
 mobile: '(max-width: 736px)'
 ));

 @include skel.skel-layout((
 reset: 'full',
 boxModel: 'border',
 grid: ( gutters: (40px, 40px) ),
 conditionals: true,
 containers: 1400px,
 breakpoints: (
 wide: (
 containers: 1200px,
 grid: (
 gutters: (40px, 40px)
 ),
 ),
 normal: (
 containers: 960px,
 grid: (
 gutters: (40px, 40px)
 ),
 ),
 narrow: (
 containers: 100%,
 grid: (
 gutters: (20px, 20px)
 ),
 ),
 narrower: (
 containers: 100%,
 grid: (
 gutters: (20px, 20px)
 ),
 ),
 mobile: (
 containers: (100%, true),
 )
 )
 ));

/* Basic */

 /* pmagunia */

 .width-100 {
 width: 100%;
 }

 #grid th.gridjs-th .gridjs-th-content {
 overflow: auto;
 text-overflow: initial;
 }

 div#dataset-info p {
 margin-bottom:0.75em;
 }

 span.mono {
 font-family: 'Courier new', monospace;
 background: rgba(0, 0, 0, 0.05);
 border-color: rgba(0, 0, 0, 0.2);
 border-radius: 4px;
 font-size: 0.9em;
 padding: 0.25em 0.325em;
 }

 div.cc-window a {
 text-decoration: none;
 }

 html {
 scroll-behavior: smooth;
 background-color: #dce3e2;
 }

 /* latin */

 #grid {
 height: 500px;
 overflow-y: scroll;
 }

 h2#title {
 font-size:1em;
 color: white;
 font-weight: 900;
 }

 #dataset-iframe .highlight pre {
 border-radius: 8px;
 border: 5px solid black;
 }

 #disqus_thread {
 background-color: #f5fafa;
 }

 .icon.upwork {
 font-size: 16px;
 font-weight: bold;
 }

 body #main #about-me {
 background: #f5fafa;
 }

 .my_home {
 height:16px;
 vertical-align:text-top;
 margin-left:2px;
 }

 @media (prefers-color-scheme: dark) {

 body #main #about-me {
 background: linear-gradient(#323639, #525659);
 }

 #disqus_thread {
 background-color: #323639;
 border-radius: 8px;
 }

 #dataset-iframe .highlight pre {
 border: 1px solid #ccc;
 }
 #dataset-info span.mono {
 border-radius: 4px;
 font-size: 0.9em;
 padding: 0.25em 0.325em;
 } 

 #dataset-info span.mono,
 #dataset-attachments code {
 background: rgba(0, 0, 0, 0.25);
 border-color: rgba(0, 0, 0, 0.5);
 }

 .gridjs table tbody td {
 background-color: #2A3338;
 color: white;
 border: 1px solid #777;
 }

 html {
 background-color: #323639;
 }

 .gridjs table thead th {
 background-color: #4A5353;
 color: white;
 }

 #grid .gridjs-wrapper {
 border: 1px solid white;
 }

 body #main > section.shade-two,
 body #main article.shade-two { 
 background-color: #323639;
 color: #f7f7f7;
 }

 body h1,
 body h2,
 body h3,
 body h4,
 body h5,
 body h6{
 color: #ffffff;
 }

 #dataset-info h2 {
 margin: 1em 0 0.35em 0;
 }

 body #footer {
 background: linear-gradient(#222629, #121619);
 box-shadow: none;
 border-top: 1px solid pink;
 }

 body #main > section.shade-two,
 #main > section.shade-two {
 background: linear-gradient(#323639, #525659);
 border-top: 1px solid pink;
 }

 body #main > section.shade-three,
 #main article.shade-three {
 background: linear-gradient(#424649, #626669);
 border-top: 1px solid pink;
 }

 body #main > section.shade-four,
 #main article.shade-four {
 background: linear-gradient(#525659, #626669);
 border-top: 1px solid pink;
 } 

 body #main #about-me {
 border-top: none;
 }

 body dl dt,
 body strong,
 body b {
 color: white;
 font-weight: 600;
 }

 html body,
 body p {
 color: #ccc;
 }

 #main > section.shade-two {
 background: linear-gradient(#323639, #525659);
 }
 }

 @font-face {
 font-family: 'Source Sans Pro';
 src: url('/assets/fonts/source-sans-pro/sourcesanspro-light-webfont.woff2') format('woff2'),
 url('/assets/fonts/source-sans-pro/sourcesanspro-light-webfont.woff') format('woff');
 font-weight: normal;
 font-style: normal;
 }
 .border-1 {
 border: 1px solid #777;
 border-radius: 4px;
 }

 .shadow-1 {
 box-shadow: 0 10px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
 }

 nav li span {
 padding-left:1em;
 }

 #subnav {
 margin-top:10px;
 overflow: auto;
 height: 150px;
 }

 a#redirect-link {
 display:none !important;
 }

 .ll {
 border-left:1px solid #ccc;
 padding-left:1em;
 }

 #logo .image img {
 border:1px solid ghostwhite;
 }

 #portfolio img {
 border:1px solid #eee;
 opacity: 0.4;
 -webkit-transition: opacity 1.5s;
 transition: opacity 1.5s;
 }

 #portfolio img:hover {
 opacity: 1.0;
 }

 #portfolio .item {
 border: 1px solid #eee;
 }

 .g-recaptcha {
 margin:1em 0;
 }

 #contact-message-feedback-form .form-item,
 #contact-message-feedback-form .form-wrapper {
 margin-bottom:1em;
 }
 body {
 background: #fff;
 font-family: 'Source Sans Pro', sans-serif;
 font-size: 19pt;
 font-weight: 300;
 line-height: 1.75em;
 color: #626262;

 &.is-loading * {
 @include skel.vendor('transition', 'none !important');
 @include skel.vendor('animation', 'none !important');
 }
 }

 input, textarea, select {
 font-family: 'Source Sans Pro', sans-serif;
 font-size: 19pt;
 font-weight: 300;
 line-height: 1.75em;
 color: #888;
 }

 h1, h2, h3, h4, h5, h6 {
 font-weight: 300;
 color: #666;
 line-height: 1.5em;
 }

 h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
 color: inherit;
 text-decoration: none;

 strong {
 color: #333;
 }
 }

 h1 {
 font-size: 2.5em;
 }

 h2 {
 font-size: 2em;
 letter-spacing: -1px;
 }

 h3 {
 font-size: 1.5em;
 }

 header {
 margin: 0 0 2em 0;

 > p {
 margin: 1em 0 0 0;
 }
 }

 footer {
 margin: 2em 0 0 0;
 }

 strong, b {
 font-weight: 300;
 color: #666;
 }

 em, i {
 font-style: italic;
 }

 a {
 text-decoration: none;
 color: inherit;
 border-bottom: dotted 3px #efb0bb;
 @include skel.vendor('transition', ('color 0.35s ease-in-out', 'border-bottom-color 0.35s ease-in-out'));
 outline: 0;

 &:hover {
 color: #E27689;
 border-bottom-color: rgba(255, 255, 255, 0);
 }
 }

 sub {
 position: relative;
 top: 0.5em;
 font-size: 0.8em;
 }

 sup {
 position: relative;
 top: -0.5em;
 font-size: 0.8em;
 }

 hr {
 border: 0;
 border-top: solid 1px #41484c;
 }
 
 code {
 background: rgba(0, 0, 0, 0.05);
 border-color: rgba(0, 0, 0, 0.2);
 border-radius: 4px;
 font-family: "Courier New", monospace;
 font-size: 0.9em;
 padding: 0.25em 0.325em;
 }

 pre {
 -webkit-overflow-scrolling: touch;
 font-family: "Courier New", monospace;
 font-size: 0.9em;
 tab-size: 4;
 margin: 0 0 2em 0;
 code {
 display: block;
 line-height: 1.5;
 padding: 0.75em 1em;
 overflow-x: auto;
 }
 }

 blockquote {
 margin-left: 1em;
 border-left: solid 0.5em #ddd;
 padding: 1em 0 1em 2em;
 font-style: italic;
 }

 p, ul, ol, dl, table {
 margin-bottom: 0.7em;
 }

 dl {
 dt {
 font-weight: 300;
 color: #666;
 }
 dd {
 margin-left: 1em
 }
 }

 br.clear {
 clear: both;
 }

/* Sections/Article */

 section, article {
 margin-bottom: 3em;

 > :last-child,
 > .container,
 &:last-child,
 > .container > :last-child {
 margin-bottom: 0;
 }

 .row > & {
 margin-bottom: 0;
 }

 .row {
 margin-top: 40px;
 }

 .row > * {
 padding-top: 0;
 }

 ul, ol {
 padding-left: 0.25em;
 margin-left: 1.25em;
 }
 }

 .image {
 display: inline-block;
 border: 0;

 img {
 display: block;
 width:100%;
 }

 &.avatar48 {
 height: 55.33px;
 width: 38.67px;
 background: #222;
 img {
 width:38.67px;
 }
 }

 &.fit {
 display: block;
 width: 100%;
 }

 &.featured {
 display: block;
 width: 100%;
 margin: 0 0 2em 0;
 }

 &.left {
 float: left;
 margin: 0 2em 2em 0;
 }

 &.right {
 float: right;
 margin: 0 0 2em 2em;
 }

 &.centered {
 display: block;
 margin: 0 0 2em 0;

 img {
 margin: 0 auto;
 width: auto;
 }
 }
 }

/* List */

 ul {
 list-style: disc;

 &.icons {
 cursor: default;

 li {
 display: inline-block;
 }

 a {
 display: inline-block;
 width: 2em;
 height: 2em;
 line-height: 2em;
 text-align: center;
 border: 0;
 }
 }
 }

 ol {
 list-style: decimal;
 }

/* Form */

 form {
 label {
 display: block;
 text-align: left;
 margin-bottom: 0.5em;
 }

 input[type="text"],
 input[type="email"],
 input[type="password"],
 select,
 textarea {
 position: relative;
 -webkit-appearance: none;
 display: block;
 border: 0;
 outline: 0;
 background: #fff;
 background: rgba(255, 255, 255, 0.75);
 width: 100%;
 border-radius: 0.35em;
 padding: 0.75em 1em 0.75em 1em;
 box-shadow: inset 0 0.1em 0.1em 0 rgba(0, 0, 0, 0.05);
 border: solid 1px rgba(0, 0, 0, 0.15);
 @include skel.vendor('transition', 'all 0.35s ease-in-out');

 &:focus {
 box-shadow: 0 0 2px 1px #063f76;
 background: #fff;
 }
 }

 input[type="text"],
 input[type="email"],
 input[type="password"],
 select {
 line-height: 1.25em;
 }

 textarea {
 min-height: 14em;
 }

 .formerize-placeholder {
 color: #ccc !important;
 }

 ::-webkit-input-placeholder {
 color: #ccc !important;
 }

 :-moz-placeholder {
 color: #ccc !important;
 }

 ::-moz-placeholder {
 color: #ccc !important;
 }

 :-ms-input-placeholder {
 color: #ccc !important;
 }

 ::-moz-focus-inner {
 border: 0;
 }
 }

/* Table */

 table {
 width: 100%;
 text-align: left;

 td {
 padding: 0.5em 1em 0.5em 1em;
 vertical-align: middle;
 }

 th {
 text-align: left;
 padding: 0.5em 1em 0.5em 1em;
 color: #fff;
 background: #222729;
 }

 thead {
 background: #444;
 color: #fff;
 }

 tfoot {
 background: #eee;
 }
 }

/* Button */

 input[type="button"],
 input[type="submit"],
 input[type="reset"],
 button,
 .button {
 position: relative;
 display: inline-block;
 border-radius: 0.35em;
 color: #fff !important;
 text-decoration: none;
 padding: 0.75em 2.5em 0.75em 2.5em;
 background-color: #063f76;
 border:2px solid white;
 cursor: pointer;
 @include skel.vendor('background-image', ('linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.15))', 'url("images/overlay.png")'));
 @include skel.vendor('transition', 'background-color 0.35s ease-in-out');
 &:hover {
 background-color: #001f46;
 border-bottom-color:white !important;
 }
 &:active {
 background-color: #053e75;
 }
 }

/* Item */

 .item {
 box-shadow: 0 0.05em 0.15em 0 rgba(0, 0, 0, 0.05);
 margin-bottom: 40px;

 header {
 color: #666;
 text-align: center;
 background: #fff;
 margin: 0;
 padding: 1em;
 font-size: 0.8em;

 h3 {
 font-size: 1em;
 }
 }
 }

/* Icons */

 .icon {
 text-decoration: none;

 &:before {
 display: inline-block;
 font-family: FontAwesome;
 font-size: 1.25em;
 text-decoration: none;
 font-style: normal;
 font-weight: normal;
 line-height: 1;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 }

 > .label {
 display: none;
 }
 }

/* Header */

 #header {
 @include skel.vendor('display', 'flex');
 @include skel.vendor('flex-direction', 'column');
 @include skel.vendor('justify-content', 'space-between');
 background: #222629;
 box-shadow: inset -0.25em 0 0.25em 0 rgba(0, 0, 0, 0.1);
 color: #fff;
 height: 100%;
 left: 0;
 overflow-y: auto;
 position: fixed;
 text-align: right;
 top: 0;
 width: 375px;

 .top {
 @include skel.vendor('flex-grow', '1');
 }

 .bottom {
 @include skel.vendor('flex-shrink', '0');
 padding: 1.5em 0 0.75em 0;

 > :last-child {
 margin-bottom: 0;
 }
 }

 .icons {
 font-size: 0.8em;
 text-align: center;

 a {
 color: #41484c;
 @include skel.vendor('transition', 'color 0.35s ease-in-out');

 &:hover {
 color: #fff;
 }
 }
 }
 }

 #logo {
 position: relative;
 margin: 1.75em 1.5em 1.5em 1.5em;
 min-height: 48px;
 cursor: default;

 a {
 border: 0;
 }

 h1 {
 position: relative;
 color: #fff;
 font-weight: 600;
 font-size: 1em;
 line-height: 1em;
 }

 p {
 position: relative;
 display: block;
 font-size: 0.6em;
 color: rgba(255, 255, 255, 0.5);
 line-height: 1.25em;
 margin: 0.5em 0 0 0;
 }

 .image {
 position: absolute;
 left: 0;
 top: 0;
 }
 }

 #nav {
 ul {
 list-style: none;
 margin-bottom: 0;
 hr {
 margin: auto 1.5em;
 }

 li {
 a {
 display: block;
 padding: 0.5em 1.5em 0.5em 1.5em;
 color: rgba(255, 255, 255, 0.5);
 text-decoration: none;
 outline: 0;
 border: 0;
 @include skel.vendor('transition', 'none');

 span {
 position: relative;
 display: block;
 font-size: 0.8em;

 &:before {
 position: absolute;
 left: 0;
 color: pink;
 text-align: center;
 width: 1.25em;
 line-height: 1.75em;
 }
 }

 &.active {
 background: rgba(0, 0, 0, 0.15);
 box-shadow: inset 0 0 0.25em 0 rgba(0, 0, 0, 0.125);
 color: #fff;

 span:before {
 color: pink;
 }
 }
 }
 }
 }
 }

/* Footer */

 #footer {
 margin-left: 375px;
 text-align: center;
 background-color: #dce3e2;
 padding: 3em 0 4em 0;
 box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.05), inset 0 0.1em 0.1em 0 rgba(0, 0, 0, 0.025);
 font-size: 0.8em;

 .copyright {
 cursor: default;
 margin: 0;

 li {
 display: inline-block;
 line-height: 1em;
 border-left: solid 1px rgba(128, 128, 128, 0.35);
 padding: 0 0 0 0.5em;
 margin: 0 0 0 0.5em;

 &:first-child {
 border-left: 0;
 padding-left: 0;
 margin-left: 0;
 }
 }
 }
 }

/* Main */

 #main {
 margin-left: 375px;
 > section {
 text-align: left;
 }
 > article {
 text-align: left;
 header {
 p {
 font-style: normal;
 }
 }
 }
 > section, article {
 margin: 0;
 overflow: hidden;
 padding: 4em 0;
 box-shadow: inset 0 1px 0 0 rgba(0, 0, 0, 0.05), inset 0 0.1em 0.1em 0 rgba(0, 0, 0, 0.025);

 &.dark {
 color: #e7e7e7;
 color: rgba(255, 255, 255, 0.85);

 h2, h3, h4, h5, h6 {
 color: inherit;
 }

 strong {
 color: #fff;
 border-color: inherit;
 }

 a {
 color: #fff;
 border-color: inherit;

 &:hover {
 border-bottom-color: rgba(255, 255, 255, 0);
 }
 }
 }

 &.cover {
 padding: 6em 0;
 position: relative;
 z-index: 0;
 img.back {
 position: absolute;
 object-fit: cover;
 width: 100%;
 height: 100%;
 left: 0;
 top: 0;
 z-index: -1;
 border-bottom: 5px solid #e27689;
 }
 }

 &.shade-one {
 background-color: #81918E;
 }

 &.shade-two {
 background-color: #f5fafa;
 }

 &.shade-three {
 background-color: #ecf1f1;
 }

 &.shade-four {
 background-color: #e8edec;
 }
 }
 }

/* Wide */

 @include skel.breakpoint(wide) {

 /* Basic */

 body, input, textarea, select {
 font-size: 14pt;
 }

 /* Header */

 #header {
 width: 300px;
 }

 /* Footer */

 #footer {
 margin-left: 300px;
 }

 /* Main */

 #main {
 margin-left: 300px;
 }

 body {
 line-height: 1.75em;
 }

 }

/* Normal */

 @include skel.breakpoint(normal) {

 /* Main */

 body, input, textarea, select {
 font-size: 14pt;
 }

 #main {
 > section, article {
 padding: 3em 0;
 }

 section {
 &.cover {
 padding: 5em 0;
 }
 }
 }

 /* Header */
 #nav {
 ul {
 hr {
 margin: auto 1.25em;
 }
 }
 }

 }

/* Narrow */

 @include skel.breakpoint(narrow) {

 /* Basic */

 body, input, textarea, select {
 font-size: 16pt;
 }

 .container {
 padding: 0 2em 0 2em;
 }

 /* List */

 ul {
 &.icons {
 li {
 a {
 width: 1.75em;
 }
 }
 }
 }

 /* Item */

 .item {
 margin-bottom: 20px;
 }

 /* Header */

 #header {
 width: 20%;
 }

 #logo {
 .image {
 position: relative;
 margin: 0 0 0.5em 0;
 }
 }

 #nav {
 ul {
 list-style: none;
 li {
 a {
 font-size: 0.8em;
 padding-top: 0.5em;
 padding-bottom: 0.5em;

 span {
 padding-right: 2.25em;

 &:before {
 left: 100%;
 margin-left: -1.25em;
 line-height: 2.25em;
 }
 }
 }
 }
 }
 }

 /* Footer */

 #footer {
 margin-left: 20%;
 }

 /* Main */

 #main {
 margin-left: 20%;
 }

 }

/* Narrower */

 #headerToggle {
 display: none;
 margin-left: 0.1rem;
 margin-top: 0.2rem;
 }

 @include skel.breakpoint(narrower) {

 /* Basic */

 html, body {
 overflow-x: hidden;
 }

 body, input, textarea, select {
 font-size: 16pt;
 }

 header {
 br {
 display: none;
 }

 }

 .container {
 padding: 0 2em 0 2em;
 }

 /* Item */

 .item {
 margin-bottom: 15px;
 }

 /* List */

 ul {
 &.icons {
 a {
 width: 1.75em;
 font-size: 1.25em;
 }
 }
 }

 /* Header */

 #header {
 @include skel.vendor('backface-visibility', 'hidden');
 @include skel.vendor('transform', 'translateX(#{functions.size(header) * -1})');
 @include skel.vendor('transition', ('transform #{functions.duration(header)} ease'));
 -webkit-overflow-scrolling: touch;
 display: block;
 height: 100%;
 left: 0;
 overflow-y: auto;
 position: fixed;
 top: 0;
 width: functions.size(header);
 z-index: functions.misc(z-index-base) + 2;
 width: functions.size(header);
 background: #222729;
 box-shadow: inset -0.25em 0 0.25em 0 rgba(0, 0, 0, 0.125);

 .top {
 position: relative;
 }

 .bottom {
 border-top: solid 1px rgba(255, 255, 255, 0.05);
 box-shadow: 0 -1px 0 0 rgba(0, 0, 0, 0.15);
 padding-top: 2em;
 margin-top: 2em;
 position: relative;
 }
 }

 #logo {
 margin: 1.5em 1.25em 1.25em 1.25em;
 }

 #nav {
 ul {
 list-style: none;
 hr {
 margin: auto 1.25em;
 }
 li {
 a {
 padding: 0.5em 1.25em 0.5em 1.25em;
 }
 }
 }
 }

 #headerToggle {
 @include skel.vendor('backface-visibility', 'hidden');
 @include skel.vendor('transition', 'transform #{functions.duration(header)} ease');
 display: block;
 height: 2.25em;
 left: 0;
 position: fixed;
 top: 0;
 width: 3.25em;
 z-index: 20001;

 .toggle {
 position: absolute;
 left: 0;
 top: 0;
 width: 100%;
 height: 100%;
 outline: 0;
 border: 0;

 }
 }

 body {
 &.header-visible {
 #main {
 @include skel.vendor('transform', 'translateX(#{functions.size(header)})');
 }

 #headerToggle {
 @include skel.vendor('transform', 'translateX(#{functions.size(header)})');
 }

 #header {
 @include skel.vendor('transform', 'translateX(0)');
 }
 }
 }

 /* Footer */

 #footer {
 margin-left: 0;
 }

 /* Main */

 #main {
 @include skel.vendor('backface-visibility', 'hidden');
 @include skel.vendor('transition', 'transform #{functions.duration(header)} ease');
 padding-bottom: 1px;
 margin-left: 0;

 > section, article {
 padding: 3em 0;
 }

 section {
 &.cover {
 padding: 4em 0;
 }
 }
 }
 }

/* Mobile */

 @include skel.breakpoint(mobile) {

 /* Basic */

 body, input, textarea, select {
 font-size: 14pt;
 }

 h2 {
 font-size: 2em;
 letter-spacing: 0;
 font-weight: 300;
 }

 h2 {
 font-size: 1.5em;
 letter-spacing: 0;
 font-weight: 300;
 }

 .container {
 padding: 0 15px 0 15px;
 }

 /* List */

 ul {
 &.icons {
 a {
 width: 2em;
 font-size: 1.25em;
 }
 }
 }

 /* Main */

 #main {
 > section, article {
 padding: 2em 0;
 }

 section {
 &.cover {
 padding: 4em 0em;

 header {
 padding: 0 1em;
 }
 }
 }
 }

 /* Footer */

 #footer {
 .copyright {
 li {
 display: block;
 line-height: 1.25em;
 border: 0;
 padding: 0;
 margin: 1em 0 0 0;

 &:first-child {
 margin-top: 0;
 }
 }
 }
 }

 }


